import React from 'react';
import {Group, List, Cell, InfoRow, Div, Avatar, Button, Gallery} from "@vkontakte/vkui"
import Icon28Favorite from '@vkontakte/icons/dist/28/favorite';
import Icon28FavoriteOutline from '@vkontakte/icons/dist/28/favorite_outline';

class MastersCard extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activeMaster: props.activeMaster
        };
    }
    render(){
        return (
            <Div>
            <Group title="">
                <Cell
                    photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                    description={this.state.activeMaster.category}
                    bottomContent={<Button>В избранное</Button>}
                    before={<Avatar src={this.state.activeMaster.avatarLink} size={80}/>}
                    size="l"
                >
                    {this.state.activeMaster.firstname} {this.state.activeMaster.lastname}
                </Cell>
            </Group>
            <Group title="">
                <List>
                    <Cell>
                        <InfoRow title="Рейтинг">
                            <Div style={{display: '-webkit-inline-box'}}>
                                <Icon28Favorite />
                                <Icon28Favorite />
                                <Icon28Favorite />
                                <Icon28Favorite />
                                <Icon28FavoriteOutline />
                            </Div>
                        </InfoRow>
                    </Cell>
                    <Cell>
                        <InfoRow title="Стаж">
                            3 года 7 месяцев
                        </InfoRow>
                    </Cell>
                    <Cell>
                        <InfoRow title="Место работы">
                            Частный мастер
                        </InfoRow>
                    </Cell>
                </List>
            </Group>
                <Group>
                    <Cell>{this.state.activeMaster.description}</Cell>
                </Group>
                <Group title="Портфолио">
                    <Gallery
                        slideWidth="90%"
                        align="center"
                        style={{ height: 250 }}
                    >
                        <div style={{ backgroundImage: 'url(https://i.pinimg.com/474x/9a/57/f0/9a57f0e84191e278d840a5536ebab34c.jpg)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(https://avatars.mds.yandex.net/get-zen_doc/1554513/pub_5d77a5dd74f1bc00ad79c9f1_5d77a5f198930900ae483c74/scale_1200)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(https://womans.ws/wp-content/uploads/2019/10/1523527373_44-1068x1068.jpg)', backgroundSize: 'cover' }} />
                    </Gallery>
                    <Cell expandable onClick={() => this.props.openMasterPhoto('masterPhoto')}>Посмотреть все работы</Cell>
                </Group>
                <Group title="Сертификаты">
                    <Gallery
                        slideWidth="90%"
                        align="center"
                        style={{ height: 500 }}
                    >
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                        <div style={{ backgroundImage: 'url(data:image/jpeg;base64,)', backgroundSize: 'cover' }} />
                    </Gallery>
                </Group>
            </Div>
        );
    }
}

export default MastersCard;